.smallPokemonCard {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	margin: 10px;
	border-radius: 10px;
	background: #4c4cffd9 !important;
	cursor: pointer;
	width: 160px;
	min-height: 240px;
	object-fit: cover;

	&:hover {
		transform: scale(1.02);
	}

	.pokemonLogo {
		width: 100%;
		min-height: 160px;
		text-align: center;

		img,
		svg {
			width: 100%;
			height: auto;
			object-fit: cover;
		}
	}

	.pokemonName {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: black;
		font-size: 24px;

		&::first-letter {
			text-transform: uppercase;
		}
	}
}

.allPokemonsWrapper {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: flex-start;
	justify-content: center;
	margin-top: 50px;
	margin-bottom: 100px;
	min-height: 100vh;
}

.loadNext {
	margin-top: 30px;
	margin-right: auto;
	margin-bottom: 70px;
	margin-left: auto;
}

.playButtons {
	top: 60px;
}

.loader {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 40px;
	margin-bottom: 80px;
	width: 100%;
	text-align: center;
	font-size: 24px;
}

.pokemon-end {
	margin-top: 40px;
	width: 100%;
	text-align: center;
	font-size: 26px;
}

.wrapper {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: flex-start;
	justify-content: center;
	margin-top: 50px;
	margin-bottom: 100px;
	width: 100%;
	max-width: 100vw;

	a {
		box-shadow: 0.5px 0 0 0 red, 0 0.5px 0 0 red, 0.5px 0.5px 0 0 red, 0.5px 0 0 0 red inset, 0 0.5px 0 0 red inset;
	}

	img {
		width: calc(100vw / 40);
		min-width: 65px;
		max-width: 65px;
		height: calc(100vh / 20);
		min-height: 65px;
		max-height: 65px;
	}

	div,
	img {
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 100px;
		object-fit: cover;
	}
}

body {
	margin: 0;
	padding: 0;
	width: 100%;
}

.viewType {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 100px;
	width: 100%;

	.viewTypeInner {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
		width: 90%;

		.innerBlock {
			display: flex;
			align-items: center;
			justify-content: center;
			border: 1px solid black;
			border-radius: 5px;
			cursor: pointer;
			padding: 5px 15px 5px 15px;

			&:hover {
				box-shadow: 5px 3px 11px 1px #000000;
			}

			&:active {
				box-shadow: 0 1px 0 #00823f;
			}

			span {
				margin-right: 10px;
				font-size: 24px;
			}

			.separator {
				margin-top: 2px;
			}

			.catalog,
			.list {
				margin-top: 4px;
				cursor: pointer;
				width: 20px;
			}
		}
	}
}
